.root {
    flex-grow: 1;
    padding: 16px;
  }
  
  .paper {
    padding: 16px;
    text-align: center;
    color: rgba(0, 0, 0, 0.87);
  }
  
  .user-item {
    font-size: 18px;
    margin-bottom: 8px;
    color: coral;
  }

  .user-grid {
    width: 80%;
    margin-left: 10%;
    border-collapse: collapse;
    margin-top: 16px; 
  }
  
  .user-grid th,
  .user-grid td  {
    background-color: black;
    padding: 8px 12px; 
    margin: auto;
    text-align: left; 
    border: 1px solid #ccc; 
    color: coral;
  }

  .user-input {
    color: coral;
  }

.button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    color: coral;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
  }
  
  .center-button {
    text-align: center;
  }

.button-70 {
  background-color: black;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, .3) 0 5px 15px;
  box-sizing: border-box;
  color: coral;
  cursor: pointer;
  font-family: Montserrat,sans-serif;
  font-size: .9em;
  margin: 5px;
  padding: 10px 15px;
  text-align: center;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.input-70 {
    border-radius: 0px;
    box-shadow: rgba(0, 0, 0, .3) 0 5px 15px;
    box-sizing: border-box;
    color: coral;
    cursor: pointer;
    font-family: Montserrat,sans-serif;
    font-size: .9em;
    margin: 5px;
    padding: 10px 15px;
    text-align: center;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }
  
  .deleteBtn {
    background: transparent;
  }
  
  
  
  